// Timer component
function CountdownTimer({ endDate, theme, style }: { endDate: string; theme: string; style: string }) {
  const [timeLeft, setTimeLeft] = useState({ days: 0, hours: 0, minutes: 0, seconds: 0 })

  useEffect(() => {
    const calculateTimeLeft = () => {
      const difference = new Date(endDate).getTime() - new Date().getTime()
      if (difference > 0) {
        setTimeLeft({
          days: Math.floor(difference / (1000 * 60 * 60 * 24)),
          hours: Math.floor((difference / (1000 * 60 * 60)) % 24),
          minutes: Math.floor((difference / (1000 * 60)) % 60),
          seconds: Math.floor((difference / 1000) % 60),
        })
      }
    }

    calculateTimeLeft()
    const timer = setInterval(calculateTimeLeft, 1000)
    return () => clearInterval(timer)
  }, [endDate])

  const selectedTheme = timerThemes.find(t => t.id === theme) || timerThemes[0]

  return (
    <div className={`${selectedTheme.bgColor} ${selectedTheme.textColor} rounded-lg p-4`}>
      <div className="flex items-center justify-center gap-4">
        {timeLeft.days > 0 && (
          <div className="text-center">
            <div className="text-3xl font-bold">{String(timeLeft.days).padStart(2, '0')}</div>
            <div className="text-xs opacity-80">Days</div>
          </div>
        )}
        <div className="text-center">
          <div className="text-3xl font-bold">{String(timeLeft.hours).padStart(2, '0')}</div>
          <div className="text-xs opacity-80">Hours</div>
        </div>
        <div className="text-2xl font-bold">:</div>
        <div className="text-center">
          <div className="text-3xl font-bold">{String(timeLeft.minutes).padStart(2, '0')}</div>
          <div className="text-xs opacity-80">Minutes</div>
        </div>
        <div className="text-2xl font-bold">:</div>
        <div className="text-center">
          <div className="text-3xl font-bold">{String(timeLeft.seconds).padStart(2, '0')}</div>
          <div className="text-xs opacity-80">Seconds</div>
        </div>
      </div>
    </div>
  )
}

